$break-large: 800px;

.menu-item {
  min-width: 30%;
	height: 240px;
	flex: 1 1 auto;
	display: flex;
	align-items: center;
	justify-content: center;
	border: 1px solid black;
	margin: 0 7.5px 15px;
	overflow: hidden;
	border-radius: 21px 21px 21px 21px;
	-moz-border-radius: 21px 21px 21px 21px;
	-webkit-border-radius: 21px 21px 21px 21px;
	border: 0px solid #000000;
	-webkit-box-shadow: 6px 9px 27px -10px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 6px 9px 27px -10px rgba(0, 0, 0, 0.75);
	box-shadow: 6px 9px 27px -10px rgba(0, 0, 0, 0.75);

  &:hover {
    cursor: pointer;

    & .background-image {
      transform: scale(1.1);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }

    & .content {
      opacity: 0.9;
    }
  }
  
  &:first-child {
    margin-right: 7.5px;
  }

  &:last-child {
    margin-left: 7.5px;
  }

  &.large {
    height: 380px;
  }

  .background-image {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
  }
  
  .content {
    height: 90px;
		padding: 0 25px;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		border: 1px solid black;
		background-color: white;
		opacity: 0.7;
		position: absolute;
		border-radius: 21px 21px 21px 21px;
		-moz-border-radius: 21px 21px 21px 21px;
		-webkit-border-radius: 21px 21px 21px 21px;
		border: 0px solid #000000;
		backdrop-filter: blur(7px) saturate(75%);
		-webkit-backdrop-filter: blur(7px) saturate(75%);
		background-color: rgba(1, 0, 0, 0.43);
		border-radius: 12px;
		border: 1px solid rgba(255, 255, 255, 0.125);

    .title {
      font-weight: bold;
      margin-bottom: 0 6px 0;
      font-size: 22px;
      color: #ffff;
    }

    .subtitle {
      font-weight: lighter;
      font-size: 16px;
      color: #ffff;
    }
  }

  @media screen and (max-width: $break-large) {
    height: 240px !important;
  }
}